img{
  height:200px;
}
.cart-wrapper{
  display:inline-flex;
 
  margin:40px;
  box-shadow:0 0 7px rgb(46, 46, 217);
  gap:10px;
}
.image-wrapper{
  width:180px;
  display:flex;
  align-items:center;
  justify-content: center;
  padding:20px;
  border-right:1px solid rgb(40, 34, 34);
}
.quantity{
  display:flex;
  align-self: flex-end;
  border-radius: 3px;
  border:1px solid gray;
}
.content{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap:10px;
  padding:10px;
  text-align: center;
 margin-top:60px;
}
.btn{
  background-color: black;
  color:white;
  border-radius:3px;
  margin-bottom: 30px;
}
.price{
  margin-bottom: auto;
}
.quantity div{
  height:20px;
  width:20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity>div:first-child,
.quantity >div:last-child{
  background-color: grey;
}
main{
  padding:20px;
  display: flex;
  flex-wrap:wrap;
  min-height:80vh;
  justify-content: center;
  align-items: center;
  gap:50px;
}
.foot{
  background-color:gray;
  color:white;
  padding:10px;
  display: flex;
  bottom: 0;
  position: fixed;
  width:100%;
  font-size:20px;
}
.foot1{
  position: relative;
  left:85%;
}